
table{
    width: 100%;
    text-align: center;
}

table tr:nth-child(even){
    background-color: rgba(118, 131, 149, 0.637);
}
table tr:nth-child(odd){
    background-color: rgba(150, 152, 156, 0.637);
}
table :is(td,tr) {
    border: none;
}
table td.name {
    text-wrap: wrap;
    word-break: break-all;
}